* {
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
  width: 100%;
  color: #2f1343;
  font-family: 'Barlow', sans-serif;
  /* overflow-x: hidden; */
}

h2 {
  font-family: 'ozwald', sans-serif;
}

h4 {
  margin: 0;
}

a {
  color: rgb(85, 26, 139);
}

p {
  line-height: 1.5;
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  top: 0;
  height: 100%;
  padding: 25vh 2em;
  /* font-size: .75em; */
  z-index: 1;
}

.nav a {
  color: #2f1343;
}

.nav svg {
  cursor: pointer;
}

.icon {
  min-height: 39px;
  display: flex;
  align-items: center;
}

.label {
  background: rgba(255, 255, 255, 0.75);
  padding: .5em;
  border-top-left-radius: 500px;
  border-bottom-left-radius: 500px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  cursor: pointer;
  position: relative;
  left: -.5em;
}

.label span {
  margin-left: .5em;
}

.word {
  white-space: nowrap;
}

.tooltip {
  position: absolute;
  left: 50px;
  top: 50px;
  width: 40%;
  display: none;
}

.tooltip.top {
  margin-top: -5px;
}

.tooltip .tooltip-arrow {
  top: 50%;
  left: 0;
  margin-top: -5px;
  border-width: 5px 5px 5px 0;
  border-right-color: white;
}

.tooltip.top .tooltip-arrow {
  top: auto;
  bottom: -5px;
  left: 50%;
  margin-left: -5px;
  border-width: 5px 5px 0;
  border-top-color: white;
}

.tooltip .tooltip-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-right-color: transparent;
  border-style: solid;
}

.tooltip .tooltip-inner {
  background: white;
  padding: .75em;
  border-radius: 10px;
}

.tooltip.on {
  display: block;
}

.tooltip.off {
  display: none;
}

.page {
  width: 100%;
  min-height: 100%;
  padding: 1em 2em 1em 5em;
}

/* .page.show {
  height: auto;
} */
.more {
  text-decoration: none;
  font-weight: bold;
  background: none;
  border: none;
  width: fit-content;
  padding: .25em .5em;
}

.more:hover {
  /* font-style: italic; */
  background: rgba(255, 255, 255, 0.75);
  border-radius: 5px;
}

/* IMG STYLING */
.imgRow {
  display: flex;
  position: relative;
  left: -6em;
  width: 99vw;
  background: white;
  align-items: center;
}

.imgRow .imgWrapper {
  width: 10%;
  flex: 1 1 0;
}

.imgWrapper {
  position: relative;
}

.imgWrapper img {
  /* object-fit: contain; */
  width: auto;
  height: auto;
  display: block;
}

.imgRow .imgWrapper img {
  width: 100%;
}

.upper img, .sideBySide img {
  max-width: 40vw;
  max-height: 80vh;
}

.imgWrapper svg {
  position: absolute;
  top: .5em;
  right: .5em;
  z-index: 1;
  opacity: .75;
}

.light {
  color: rgb(231, 230, 243);
}

.imgPopup {
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  margin: 0 !important;
}

.imgPopup img {
  max-width: 90%;
  max-height: 90%;
  width: auto;
  height: auto;
  display: block;
}

.imgPopup svg {
  position: absolute;
  top: 2em;
  right: 2em;
  color: rgb(231, 230, 243);
}

.upper .imgWrapper, .sideBySide .imgWrapper {
  flex: 0 0 auto;
  margin: 0 1em;
}

.upper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text, .textBox {
  margin: 0 1em;
}

.flex {
  display: flex;
  align-self: center;
  justify-content: center;
  width: 100%;
}

.flex img {
  height: 50vh;
  width: 24vh;
}

/* specific styling for gifs that need to be cropped */
.imgContainer {
  overflow: hidden;
  height: 250px;
  width: 132px;
  margin: 0 .75em;
  /* flex-basis: 0 0 auto; */
}

.imgContainer img {
  margin: -35px 0 0 -10px;
}

/* image screens without crop */
.screenImg {
  margin: 0 .5em;
  height: 60vh;
  border: 2px solid grey;
}

.fakePhone {
  background: white;
  border-radius: 20px;
  margin: 0 .5em;
}

.circleBtn {
  border-radius: 500px;
  border: 2px solid grey;
  height: 30px;
  width: 30px;
  margin: .5em auto;
  /* position: relative;
  top: -40px; */
}

.cameraLine {
  border-radius: 500px;
  border: 2px solid grey;
  /* height: 30px; */
  width: 30px;
  margin: .75em auto;
}

.sideBySide {
  display: flex;
  align-items: center;
  margin: 2em 0;
}

.right {
  flex-flow: row-reverse;
}

.text {
  display: flex;
  flex-direction: column;
}

.reverse {
  flex-direction: row-reverse;
}

.fa-arrow-down {
  margin-top: 1em;
}

.reverse .fa-arrow-down {
  align-self: flex-end;
  margin-right: .25em;
}

.lower {
  display: flex;
  flex-direction: column;
  padding-left: 1em;
  padding: 0 3em 0 1em;
}

#graph {
  background: white;
}

.fa-arrow-up {
  align-self: center;
}

#aboutContent {
  position: relative;
  top: -20vh;
}

#portrait {
  height: 100%;
  max-height: 100%;
  min-width: 85vh;
  position: relative;
  bottom: -1em;
  flex: 1 0 auto;
}

@media screen and (max-width: 900px) {
  #portrait {
    height: 80%;
    min-width: 68vh;
    bottom: calc(-10% - 1em);
  }
}